:root {
  --color-text: #000;
  --color-activ: #00acdc;
  font-family: Roboto, sans-serif;
}

body {
  box-sizing: border-box;
  margin: 0;
}

.header {
  text-transform: uppercase;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
  display: flex;
}

.logo {
  align-items: center;
  display: flex;
}

.nav_list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
}

.nav_item {
  margin-right: 20px;
}

.nav_link {
  color: var(--color-text);
  align-items: center;
  height: 60px;
  line-height: 60px;
  text-decoration: none;
  display: flex;
  position: relative;
}

.is-active {
  color: var(--color-activ);
  text-decoration: none;
}

.logo:hover {
  border: 2px solid var(--color-activ);
}

.nav_link:hover {
  color: var(--color-activ);
  transition: all .3s;
}

.is-active:after {
  content: "";
  background-color: var(--color-activ);
  text-align: center;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
  left: 0;
}

.nav_list .nav_item:last-child {
  margin-right: 0;
}
